@import url(https://fonts.googleapis.com/css?family=Droid+Serif);
body {
	background-color: #F5F5F9;
	font-family: 'Droid Serif', sans-serif;
	font-weight: 300;
	line-height: 1.5em;
}

main {
	margin: 0px auto;
}

h1, h2, h3 {
	font-family: 'Droid Serif', serif;
	letter-spacing: 0.5px;
	font-weight: normal;
}

main.home h1 {
	margin: 4em 0.6em;
	padding: 40px 0px 50px 0px;
	font-weight: 500;
	line-height: 1.8em;
	border-top: 0px #323031 solid;
	border-bottom: 0px #323031 solid;
	color: #323031;
}

h2 {
	font-size: 2em;
	margin: 1.2em 0em 1.4em 0em;
	line-height: 1.6em;
}

h3 {
	font-size: 1.4em;
	margin: 2em 0em 1em 0em;
}

h4 {
	font-size: 1.2em;
	margin: 2.2em 0em 1em 0em;
	font-weight: 400;
}

h5 {
	display: inline-block;
	font-size: 0.9em;
	margin: 0.4em 1.3em 0px 0px;
	font-weight: 400;
}

p {
	margin-bottom: 0.2em;
  margin-top: 0.2em;
}

strong {
	font-weight: 400;
}

a {
	font-weight: 400;
	text-decoration: none;
	color: #323031;
}

a.visited {
	color: #323031;
}

h5 a {
	color: #A3A3A6;
}

h1 a:hover, h2 a:hover {
	border-bottom: 3px  solid;
}

h3 a:hover, h5 a:hover, li a:hover, p a:hover {
	border-bottom: 2px solid;
}

h2.making, div.making a:hover, main.home a.making, h5 a.making:hover {
	color: #168ADE;
}

h2.working, div.working a:hover, main.home a.working, h5 a.working:hover {
	color: #FF6764;
}

h2.reading, div.reading a:hover, main.home a.reading, h5 a.reading:hover {
	color: #FDBD74;
}

h2.writing, div.writing a:hover, main.home a.writing, h5 a.writing:hover {
	color: #54BF86;
}

h2.talking, div.talking a:hover, main.home a.talking, h5 a.talking:hover {
	color: #9C93DE;
}

a.home:hover {
	color:  #323031;
}

a.button {
	font-family: 'Droid Serif', serif;
	font-size: 1.2em;
	padding: 10px;
	width: 200px;
	margin: 25px auto 50px auto;
	text-align: center;
	border: 1px solid black;
	background-color: #FAFAFC;
	display: block;
}

div.nav {
	border-bottom: 3px #E6E6E6 solid;
}

div.content {
	margin: 3em 0em;
}

div.bottom-nav {
	padding: 1em 0em;
	border-top: 3px #E6E6E6 solid;
}

div.bottom-nav h3 {
    font-size: 1em;
    margin: 1.2em 0em;
    padding: 0em;
}

div.bottom-nav h3 a:first-child {
    float: right;
}

div.bottom-nav h3 a {
    color: #323031;
}

ul.nodots {
	list-style: none;
	padding: 0px;
}

ul {
	list-style: circle; padding-left: 1em;
}

li {
	margin: 1em 0em;
	color: #323031;
}

li.book {
   background:url('/static/images/book-icon.png') 0% 0% no-repeat no-repeat;
   padding-left: 40px;
   line-height: 32px;
   margin: 20px 0px;
}

li.blog {
   background:url('/static/images/article-icon.png') 0% 0% no-repeat no-repeat;
   padding-left: 40px;
   line-height: 32px;
   margin: 20px 0px;
}

li.video {
   background:url('/static/images/video-icon.png') 0% 0% no-repeat no-repeat;
   padding-left: 40px;
   line-height: 32px;
   margin: 20px 0px;
}

li.podcast {
   background:url('/static/images/headphones-icon.png') 0% 0% no-repeat no-repeat;
   padding-left: 40px;
   line-height: 32px;
   margin: 20px 0px;
}

figure {
	margin: 0px;
	padding: 1em 0em 1em 0em;
}

figcaption {
	font-size: 0.85em;
}

span.spacer {
	padding: 0em 0.5em;
}

iframe {
	border:1px solid #CCC;
	border-width:1px;
	margin-bottom:0.25em;
	height: 27em;
	width: 100%;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.quotes {
  color: #168ADE;
}

.rundown {
  color: #FDBD74;
}

.plans {
  color: #FF6764;
}


/* ================================================================== */
/* This file has a mobile-to-tablet, and tablet-to-desktop breakpoint */
/* ================================================================== */
@media (max-width: 400px) {
  @-ms-viewport {
    width: 320px;
  }
}
.clear {
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
}

.grid-container:before, .mobile-grid-50:before, .mobile-grid-100:before, .tablet-grid-100:before, .tablet-grid-33:before, .grid-25:before, .grid-100:before, .grid-offset:before, .clearfix:before,
.grid-container:after,
.mobile-grid-50:after,
.mobile-grid-100:after,
.tablet-grid-100:after,
.tablet-grid-33:after,
.grid-25:after,
.grid-100:after,
.grid-offset:after,
.clearfix:after {
  content: ".";
  display: block;
  overflow: hidden;
  visibility: hidden;
  font-size: 0;
  line-height: 0;
  width: 0;
  height: 0;
}

.grid-container:after, .mobile-grid-50:after, .mobile-grid-100:after, .tablet-grid-100:after, .tablet-grid-33:after, .grid-25:after,.grid-100:after,  .grid-offset:after, .clearfix:after {
  clear: both;
}

.grid-container {
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  padding-left: 10px;
  padding-right: 10px;
}

.mobile-grid-50, .mobile-grid-100, .tablet-grid-100, .tablet-grid-33, .grid-25,  .grid-100 {
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
}

.grid-parent {
  padding-left: 0;
  padding-right: 0;
}

.grid-offset {
  clear: both;
  margin-left: -10px;
  margin-right: -10px;
}

@media (max-width: 510px) {
  .mobile-grid-50 {
    float: left;
    width: 50%;
  }

  .mobile-grid-100 {
    clear: both;
    width: 100%;
  }

  body {
    font-size: 14px;
  }

  main {
    width: 90%;
  }
  main.home h1 {
    font-size: 1.8em;
  }
  h2 {
    font-size: 1.8em;
  }

  img {margin-left: 0px;}

}


@media  (min-width: 511px) and (max-width: 767px) {

  .mobile-grid-50 {
    float: left;
    width: 50%;
  }

  .mobile-grid-100 {
    clear: both;
    width: 100%;
  }

  body {
    font-size: 14px;
  }

  main {
    width: 85%;
  }
  main.home {
    width: 85%;
  }

}

@media (min-width: 768px) and (max-width: 1024px) {

  .tablet-grid-33 {
    float: left;
    width: 33.33333%;
  }

  .tablet-grid-100 {
    clear: both;
    width: 100%;
  }
}
@media (min-width: 1025px) {

  .grid-25 {
    float: left;
    width: 25%;
  }

  .grid-100 {
    clear: both;
    width: 100%;
  }

  main {
    width: 720px;
  }
}
.footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.flex-center {
  display: flex;
  justify-content: center;
}

.icons {
  width: 20%;
}

